body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
overflow:hidden;
max-height: 100%;
max-width: 190px;
min-height: 180px;
position: relative;
display: -webkit-flex;
display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
min-width: 100%;
word-wrap: break-word;
box-shadow: 0 0 6px 2px black;
background-clip: border-box;
border: 10px solid;
border-radius: 20px;
}
.img-container {
	vertical-align: middle;
	max-height: 180px;
	min-height: 180px;
	min-width:190px;
	max-width: 100%;
	overflow: hidden;
	border: 2px;
}
img {
	min-width: 100%;
	max-height: 180px;
	min-height:180px;
 	max-width: 100%;
	vertical-align: middle;
}
#image-card:hover {
	border: 10px solid #af3d48b4
}

.CardContainer {
    max-height: 140vh;
    margin-bottom: 0px;
    padding: 30px;
    min-height: 100%;
    box-shadow: inset 0px 0px 14px 4px #161617cc;
    background: linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898;
    background-blend-mode: multiply,multiply;
}
.container {
    max-height: 114vh;
    padding-top: 20px;
    min-width: 60vw;
    max-width: 60vw;
    min-height: 75px;
    box-shadow: -2px -1px 17px 4px black;
}
#scoreCard{
    padding-top: 10px;
    font-family: 'Cinzel', sans-serif;
    text-transform: uppercase;
    font-weight: bold;
    line-height: 0;
    background-color: #e46471b4;
    box-shadow: -2px -1px 17px 4px #161617cc;
}
.gameMessage  {
    text-align: left;
    font-size: 20px;
    text-shadow: 0 0 1px black;
    color: black;
    padding: 25px 0;
    margin-bottom: 10px;
}

 .gameScores {
    text-align: right;
    font-size: 20px;
    text-shadow: 0 0 1px black;
    color: black;
    padding: 25px 0;
    margin-bottom: 10px;
}
/*JUMBOTRON*/
.jumbotron {
	background-image:url(/clicky-game-challenge/static/media/Picture3.507dcbb1.png);
	background-color: #546e7acc;
	background-blend-mode: lighten;
	box-shadow: inset 0px 0px 14px 4px #161617cc;
	margin-bottom: 0px;
	text-align: center;
	padding: 3rem 2rem;
}
h1 {
	line-height: 3.5rem;
	font-family: 'Cinzel' 	;
	font-weight: 900;
	text-shadow: 2px 5px 10px #181c1f;
	font-size: 3.5rem;
}
.mainContainer {
  padding: 0;
  background-color: #ECEFF1;
  min-height: 100vh;
  padding-bottom: 100px;
  }




